import React from "react";
import './css/Terms.css'

const PrivacyPolicy = () => {

    return (
        <div>
            <div className="card">
                <div className="primary-heading">Privacy Policy</div>
                <p className="paragraph">
                    <p>Your visit to our website will provide us with information about you. Your actions when visiting our site will determine how we handle the information we learn about you.</p>
                    <p>We collect and store the following information about you when you visit our site to read or download information:</p>
                    <ul>
                        <li>You access the internet through a domain name</li>
                        <li>When you access our site and at what time</li>
                        <li>Your direct link from another website to ours.</li>
                        <li>Visited pages and Your IP address</li>
                    </ul>
                    <p>You may also receive the following information if you use Facebook to login to Barteronline.ca</p>
                    <p>Please provide your basic information and your e-mail address</p>
                    <p>You may have the app post ads on your behalf, including those you posted. If you identify yourself with a request by e-mail, then we will only use the information to reply to your request.</p>
                    <p>It is for statistical purposes that Barteronline collects information. Summary statistics are generated using software programs, and can be used for many purposes, including assessing how many visitors visit each section of our site, determining what information is of most and least interest, determining technical specifications, and detecting problems with our systems.</p>
                    <p>Our social media pages, such as Facebook, Twitter, Pinterest, and others, may feature your posted ads.</p>
                    <p>The network traffic of Barteronline is monitored by software programs to detect any unauthorized attempts to upload or change information, or to otherwise cause harm. This monitoring is for site security and to ensure that this service remains available to all users.</p>
                    <p>You will not be provided with personally identifying information by Barteronline when visiting our site unless you choose to do so, and we will never sell or otherwise transfer such information to an unaffiliated third party without your consent.</p>
                    <p>Our marketing communications will be sent to you unless you state otherwise. Alternatively, you can indicate your preference by following the instructions provided with each marketing communication if you do not wish to receive marketing communications from us.</p>
                    <h3>Cookies and Web Beacons</h3>
                    <p>Visitors' preferences are stored in cookies, access and visit information are gathered, content is customized according to the visitor's browser type, and other information sent via the visitor's browser is collected.</p>
                    <h3>Double-Click DART Cookie</h3>
                    <ul>
                        <li>Ads served by Google on Barteronline are third-party cookies.</li>
                        <li>DART cookies are used by Google to create ads that are targeted to users based on their visits to these classifieds and other sites on the internet.</li>
                        <li>At https://www.google.org/privacy_ads.html, users can opt out of the use of the DART cookie.</li>
                    </ul>
                    <p>Cookies and web beacons are used by some of our advertising partners on our site, such as Google AdSense</p>
                    <p>The advertisements and links appearing on these classifieds are sent directly to your browser by these third-party ad servers and ad networks. This results in them receiving your IP address automatically. Third-party ad networks may also use a variety of technologies (including cookies, JavaScript, and Web Beacons) to measure the effectiveness of their ads and/or to tailor advertising content that appears to you.</p>
                </p>
            </div>

        </div>
    );
};

export default PrivacyPolicy;